import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import React from 'react';

const BombIcon = ({ className }: Readonly<{ readonly className?: string }>) => (
  <button type="button" className={className}>
    <FontAwesomeIcon icon={['fas', 'bomb']} />
  </button>
);

export default BombIcon; // CellFactory is already optimized, no need for React.memo here;
