import './Minesweeper.scss';

import Footer from 'components/Footer';
import Header from 'components/Header';
import { defaultMineFieldProps, mineFieldsProps } from 'const';
import Controls from 'containers/Controls';
import MineField from 'containers/MineField';
import MineFieldProps from 'containers/MineFieldProps';
import { MinesweeperProvider } from 'contexts/minesweeper';
import { useMineField } from 'hooks/use-mine-field';
import React from 'react';

const Minesweeper = () => {
  const mineField = useMineField(defaultMineFieldProps);

  return (
    <MinesweeperProvider value={{ ...mineField, mineFieldsProps }}>
      <Header />

      <main className="Minesweeper">
        <Controls />
        <MineField />
        <MineFieldProps />
      </main>

      <Footer />
    </MinesweeperProvider>
  );
};

export default Minesweeper;
