import React, { memo } from 'react';

type RadioButtonProps<T> = Readonly<{
  readonly item: T;
  readonly name: string;
  readonly checked?: boolean;
  readonly className?: string;
  readonly onChange: (item: T) => void;
}>;

const RadioButton = <T extends Readonly<{ readonly name: string }>>({
  item,
  name,
  checked,
  className,
  onChange,
}: RadioButtonProps<T>) => {
  const handleChange = () => {
    onChange(item);
  };

  return (
    <div className={className}>
      <input
        type="radio"
        name={name}
        className={className ? `${className}Radio` : ''}
        defaultChecked={checked}
        onChange={handleChange}
      />
      &nbsp;
      {item.name}
    </div>
  );
};

export default memo(RadioButton) as typeof RadioButton;
