import './CellFactory.scss';

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import BombIcon from 'components/BombIcon';
import React, { memo } from 'react';
import { CellChecker } from 'utils/cell-checker';
import { joinClassNames } from 'utils/join-class-names';
import { CellEvent, CellVM } from 'view-models';

type CellFactoryProps = Readonly<{
  readonly cell: CellVM;
  readonly withBorderRight?: boolean;
  readonly withBorderBottom?: boolean;
  readonly onClick: (cell: CellVM) => void;
  readonly onMouseDown: (event: CellEvent, cell: CellVM) => void;
  readonly onContextMenu: (cell: CellVM) => void;
}>;

const CellFactory = (props: CellFactoryProps) => {
  const {
    cell,
    withBorderRight,
    withBorderBottom,
    onClick,
    onMouseDown,
    onContextMenu,
  } = props;

  const { value } = cell;

  const {
    isHidden,
    isEmpty,
    isFlagged,
    isMined,
    isIncorrect,
    isBusted,
  } = new CellChecker(cell);

  const classNamePrefix = 'Cell';

  const classNameSuffix = joinClassNames(
    withBorderRight ? 'Cell__border-right' : '',
    withBorderBottom ? 'Cell__border-bottom' : ''
  );

  const handleClick = () => {
    onClick(cell);
  };

  const handleContextMenu = () => {
    onContextMenu(cell);
  };

  const handleMouseDown = (event: CellEvent) => {
    onMouseDown(event, cell);
  };

  if (isHidden) {
    return (
      <button
        type="button"
        aria-label="Cell"
        className={joinClassNames(classNamePrefix, classNameSuffix)}
        onClick={handleClick}
        onContextMenu={handleContextMenu}
      />
    );
  }

  if (isFlagged) {
    return (
      <button
        type="button"
        className={joinClassNames(classNamePrefix, classNameSuffix)}
        onContextMenu={handleContextMenu}
      >
        <FontAwesomeIcon icon={['far', 'flag']} />
      </button>
    );
  }

  if (isEmpty) {
    return (
      <button
        type="button"
        aria-label="Cell"
        className={joinClassNames(
          classNamePrefix,
          `${classNamePrefix}__empty`,
          classNameSuffix
        )}
      />
    );
  }

  if (isMined) {
    return (
      <BombIcon className={joinClassNames(classNamePrefix, classNameSuffix)} />
    );
  }

  if (isIncorrect) {
    return (
      <BombIcon
        className={joinClassNames(
          classNamePrefix,
          `${classNamePrefix}__incorrect`,
          classNameSuffix
        )}
      />
    );
  }

  if (isBusted) {
    return (
      <BombIcon
        className={joinClassNames(
          classNamePrefix,
          `${classNamePrefix}__busted`,
          classNameSuffix
        )}
      />
    );
  }

  return (
    <button
      type="button"
      className={joinClassNames(
        classNamePrefix,
        `${classNamePrefix}__${value}`,
        classNameSuffix
      )}
      onMouseDown={handleMouseDown}
    >
      {value}
    </button>
  );
};

export default memo(CellFactory);
