import './MineFieldProps.scss';

import RadioButton from 'components/RadioButton';
import { MinesweeperContext } from 'contexts/minesweeper';
import React, { useCallback, useContext } from 'react';
import { MineFieldPropsVM } from 'view-models';

const MineFieldProps = () => {
  const { mineFieldsProps, mineFieldProps, setMineFieldProps } = useContext(
    MinesweeperContext
  );

  const classNamePrefix = 'MineFieldProps';

  const handleMineFieldPropsChange = useCallback(
    (props: MineFieldPropsVM) => {
      setMineFieldProps(props);
    },
    [setMineFieldProps]
  );

  return (
    <form className={classNamePrefix}>
      {Array.from(mineFieldsProps.values()).map((item) => {
        const { name, uuid } = item;

        return (
          <RadioButton
            item={item}
            name="MineFieldProps"
            checked={name === mineFieldProps.name}
            className={`${classNamePrefix}RadioButton`}
            onChange={handleMineFieldPropsChange}
            key={uuid}
          />
        );
      })}
    </form>
  );
};

export default MineFieldProps;
